/* Указываем box sizing */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Убираем внутренние отступы */
ul[class],
ol[class] {
  padding: 0;
}

/* Убираем внешние отступы */
body,
h1,
h2,
h3,
h4,
p,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

/* Выставляем основные настройки по-умолчанию для body */
body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Удаляем стандартную стилизацию для всех ul и il, у которых есть атрибут class*/
ul[class],
ol[class] {
  list-style: none;
}

/* Элементы a, у которых нет класса, сбрасываем до дефолтных стилей */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Упрощаем работу с изображениями */
img {
  max-width: 100%;
  display: block;
}

/* Указываем понятную периодичность в потоке данных у article*/
article > * + * {
  margin-top: 1em;
}

/* Наследуем шрифты для инпутов и кнопок */
input,
button,
textarea,
select {
  font: inherit;
}

/* Удаляем все анимации и переходы для людей, которые предпочитай их не использовать */
@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}

@font-face {
  font-family: "SSBOYS";
  src: url("../public/font/OldLondon.ttf") format("truetype");
  font-style: auto;

}
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@600;700&display=swap');
%font-basic{
  font-family: 'Inter', sans-serif;
}
%font-style {
  font-family: "SSBOYS";
  color: black;
  font-style: normal;
  font-size: 32px;
  font-weight: 400;
  -webkit-text-stroke: 0.5px white;
  text-shadow: 5px 5px 5px black, -2px -2px 4px black;
}


body {
  background-image: url("../public/background.png");
  background-color: white;
  background-repeat: no-repeat;
  background-size: 100%;
  background-attachment: fixed; /* Добавляем эту строку */
  min-height: 100%;
}


header {
  @extend %font-style;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 100%;
  margin-top: 25px;

  img {
    height: 100%;
    width: 25%;
    margin: auto;
    margin-left: 15px;
    margin-right: 15px;
  }

  input {
    margin: auto;
    margin-top: 25px;
    width: 750px;
    height: 50px;
    border-radius: 20px;
  }
  p{
    margin-top: 20px;
  }
 button{
   margin-top: 20px;
   @extend %font-basic;
    cursor: pointer;
   background-color: #1ED760;
   border-radius: 15px;
 }
}

nav {
  display: flex;
  width: 95%;
  margin: 25px auto;
  align-items: center;

  ul {
    @extend %font-style;

    display: flex;
    width: 50%;
    margin: 0 auto;
    justify-content: space-around;

  }
}

main {
  width: 1240px;
  margin: 70px auto;
  display: flex;
  justify-content: space-evenly;

  article {
    font-family: "Arial Black";


    width: 40%;
    height: 550px;

    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(5, 1fr);
    grid-column-gap: 15px;
    grid-row-gap: 10px;
    font-size: 15px;
    div{
      display: flex;
      flex-direction: column;
      padding: 10px;
      margin:  auto;
      align-items: center;
      background: rgba(255, 255, 255, 0.5);
      border-radius: 25px;
      width: 200px;
      height: 200px;
      cursor: pointer;
    }
    img{

      width: 150px;
      height: 150px;
    }
  }

  aside {
    @extend %font-basic;
    width: 30%;
    margin-left: 25px;


    div{
      background: rgba(255, 255, 255, 0.5);
      border-radius: 15px;
      padding: 10px;
      display: flex;
      justify-content: space-between;
      margin-right: 15px;
      margin-left: 15px;
      margin-top: 15px;
      margin-bottom: 15px;
      cursor: pointer;
    }

    img{
      width: 50px;
      height: 50px;
    }
  }

}


footer {
  @extend %font-basic;

  font-size: 15px;
  border-radius: 15px;
  .player{
  p{
    width: 25%;
    margin-top: 15px;
  }
  }
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 65px;
  background: rgba(255, 255, 255, 0.5);
  div{
    display: flex;
    margin-top: 5px;
    flex-direction: row;
    width: 100%;
  }
  img{
    margin-top: 5px;
    margin-right: 15px;
    width: 50px;
    height: 50px;
  }
  button{
    background-color: black;
  }
  .player {
    display: flex;
    margin: 0 auto;
    width: 1240px;
  }
}
